import React from "react";
import { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';
import { Link } from "react-router-dom";
import Spinner from './Spinner.js';

class Timeline extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  componentDidMount() {

  }

  render() {
    const events = this.props.events;
    const data = this.props.data;
    const selectEvents = this.props.event ? this.props.event : "";
    if (this.props.ready) {
      return (
        <VerticalTimeline>
          {
            data.map(obj => {
              const eventTitle = events.find(event => event.id === obj.event_id) ? events.find(event => event.id === obj.event_id).title : '';
              if(!selectEvents || (eventTitle && eventTitle === selectEvents)){
                return (
                  <VerticalTimelineElement
                    key={obj.title}
                    className="vertical-timeline-element--work"
                    date={obj.date}
                  >
                    <h3 className="vertical-timeline-element-title"><Link to={"/d/"+obj.title}>{obj.title}</Link></h3>
                    <h6 className="vertical-timeline-element-subtitle"><Link to={"/events/"+eventTitle}>{eventTitle}</Link></h6>
                    <p>
                      {obj.desc}
                    </p>
                  </VerticalTimelineElement>
                )
              }
              return "";
            })
          }
        </VerticalTimeline>
      );
    }
    return (
      <div className="text-center">
        <br />
        <Spinner />
          <br />
      </div>
    )
  }
}

export default Timeline;
